import styled, { css } from 'styled-components';
import { Button as RawButton, IButtonProps } from '../button/Button';
import { CSS_BTN_TYPE } from '../button/styled';
import { COLORS } from '../../common';

export const Button = styled(RawButton)`
  background-color: ${COLORS.TRANSPARENT};
  &:not([disabled]) {
    &:hover {
      background: ${COLORS.TRANSPARENT};
    }
    &:active {
      background: ${COLORS.TRANSPARENT};
    }
  }
  padding: 0;
  overflow: hidden;
  box-sizing: border-box;
  height: unset;
`;

type IInnerProps = Required<{ btnType: IButtonProps['type'], size: IButtonProps['size'] }>;

export const Main = styled.div<IInnerProps>`
  align-self: stretch;
  display: flex;
  align-items: center;
  ${CSS_BTN_TYPE}
  padding: ${({ size }) => size === 'small' ? '0 8px' : '0 12px'};
  & > svg {
    margin-right: 8px;
  }
  height: ${({ size }) => size === 'small' ? '28px' : '32px'};
`;

const CSS_BORDER_LEFT_SECONDARY = css<IInnerProps>`
  border-left: 1px solid ${({ btnType }) => {
    if (btnType === 'sunk' || btnType === 'text') {
      return COLORS.TRANSPARENT;
    }
    if (btnType === 'primary' || btnType === 'danger') {
      return COLORS.LIGHT.LEVEL4_BASE_COLOR;
    }
    return COLORS.COMMON.LINE_HARD_COLOR;
  }};
`;

export const Secondary = styled.div<IInnerProps>`
  align-self: stretch;
  display: flex;
  align-items: center;
  padding: 0 4px 0 4px;
  ${CSS_BTN_TYPE}
  ${CSS_BORDER_LEFT_SECONDARY}
`;